    --opacity-color: rgba(240, 248, 255, 0.311);
}

html {
    background-color: black;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    margin:0;
}

body {
    height: 85vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

h1 {
    color:white;
    margin: 0;
    font-family: "Libre Franklin", sans-serif;
    z-index: 1;
    float: top;
    margin-left: 5%;
    font-size: 5vh;
}

.week-container {
    display: inline-flex;
    justify-content: space-between;
    align-self: center;
    width:90vw;
}

.day-div {
    display:table-cell;
    background-color: var(--opacity-color);
    border-radius: 7%;
    width:10vw;
    max-width: 200px;
    height: 10vw;
    max-height: 200px;
    color:white;
    font-size: calc(10vw/10);
    padding:3px;
    text-align: right;
    font-family: "Public Sans", sans-serif;
    box-shadow: inset -2px -2px 5px rgba(255, 255, 255, 0.468);
}

.searchbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--opacity-color);
    border-radius: 7px;
    border:none;
    width:15vw;
    height:20px;
    min-width: 110px;
    align-self: flex-end;
    margin-right: 10px;
    padding:5px;
    z-index: 1;
    float: top;
    box-shadow: inset -2px -2px 10px rgba(255, 255, 255, 0.468);
}

input[type="search"] {
    background-color: transparent;
    border: none;
    outline: none;
    width:12vw;
    min-width: 90px;
    height: 20px;
    color:white;
}

input[type="search"]::-webkit-input-placeholder {
    color:white;
}

button[type="submit"] {
    border:none;
    background-color: transparent;
    width:15px;
    height:15px;
    margin-right: 10px;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) no-repeat;
    background-size: contain;
}

#today {
    display: flex;
    align-self: center;
    flex-direction: column;
    height:45vw;
    width: 45vw;
    max-height: 400px;
    max-width: 400px;
    font-family: "Public Sans", sans-serif;
    color:white;
    background-color: var(--opacity-color);
    border-radius: 5%;
    border: none;
    box-shadow: inset -2px -2px 10px rgba(255, 255, 255, 0.468);
    margin-bottom: 5%;
}

#div-header {
    font-size: calc(40vh / 15);
    height:10%;
    margin:0%;
    padding:5%;
}

.weather-container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    height: 90%;
    width: 100%;
}

.condition-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width:50%;
    height:100%;
}

.condition-container > p {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size:90%;
    width:100%;
    height:10%;
}

.weather-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap:9%;
    width:fit-content;
    height:100%;
}

.weather-info > p {
    margin: 0%;
    display: flex;
    align-items: center;
    font-size:90%;
    width:100%;
    height:10%;
}

.weather-temp-c::before {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    margin-right: 10px;
}

.weather-temp-f::before {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
    margin-right: 10px;
}

.weather-humidity::before {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
    margin-right: 10px;
}

.weather-wind-speed::before {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
    margin-right: 10px;
}

.weather_condition {
    width: calc(100vw / 20);
    aspect-ratio: 1 / 1;
}

.temp_c {
    width:100%;
    height:25%;
    font-size: 100%;
    text-align: center;
    margin-top: 20%;
}
